header {
  height: 216px;
  max-width: 320px;
  background-image: url("mobile-library-bg.0b7e0aad.jpg");
  background-repeat: no-repeat;
  background-size: 100% 216px;
  margin: 0 auto;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 72dpi), (min-resolution: 2dppx) {
  header {
    background-image: url("2x-mobile-library-bg.a3ab9d3f.jpg");
  }
}

.header-logo {
  align-items: center;
  gap: 5px;
  display: flex;
}

.header-logo p {
  margin: 0;
  font-size: 30px;
  font-weight: 500;
  display: none;
}

.header-links {
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  font-size: 12px;
  display: flex;
}

.header-links a {
  color: inherit;
  text-decoration: none;
}

.header-nav {
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 55px;
  display: flex;
}

#my-library {
  position: relative;
}

#my-library:after {
  content: "";
  height: 3px;
  width: 100%;
  background: #ff001b;
  position: absolute;
  bottom: -3px;
  left: 0;
}

@media (min-width: 769px) and (max-width: 1280px) {
  header {
    max-width: 768px;
    background-image: url("tablet-library-bg.fa153e04.jpg");
  }

  @media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 72dpi), (min-resolution: 2dppx) {
    header {
      background-image: url("2x-tablet-library-bg.7053d9da.jpg");
    }
  }

  .header-logo p {
    display: flex;
  }

  .header-nav {
    padding-bottom: 40px;
  }
}

@media (min-width: 1281px) {
  header {
    max-width: 1280px;
    background-image: url("desktop-library-bg.a0e5ac79.jpg");
  }

  @media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 72dpi), (min-resolution: 2dppx) {
    header {
      background-image: url("2x-desktop-library-bg.57c83e7a.jpg");
    }
  }

  .header-logo p {
    display: flex;
  }

  .header-nav {
    padding-bottom: 40px;
  }
}

